import React from 'react'
import { Backbone } from '../../../../../atoms'
import {
  Column,
  Container,
  Row,
  Spacer
} from '../../../../../layout'

interface ILoadingProps {
  loadingNumbers: number;
}
export const LoadingAddress = ({ loadingNumbers = 7 }: ILoadingProps) => {
  return (
    <Column overflow='hidden'>
      <Backbone
        borderRadius='5px'
        height={10}
        width={75}
      />
      <Spacer.Horizontal size={32} />

      {Array.from(Array(loadingNumbers).keys()).map(value => {
        return (
          <React.Fragment key={value}>
            <Loader />
            <Spacer.Horizontal size={48} />
            <Loader secondaryWidth={80} width={75} />
            <Spacer.Horizontal size={48} />
          </React.Fragment>
        )
      })}

    </Column>
  )
}
interface LoaderProps {
  secondaryWidth?: number;
  width?: number;
}
export const Loader = ({ width = 60, secondaryWidth = 90 }: LoaderProps) => {
  return (
    <Row>
      <Container
        justifyContent='between'
        maxHeight={'30px'}
        minHeight={'30px'}
        minWidth={'30px'}
        position='relative'
        width={'30px'}
      >
        <Backbone
          borderRadius='50%'
          height={30}
          width={100}
        />
      </Container>
      <Spacer.Vertical size={16} />
      <Column>
        <Backbone
          borderRadius='0'
          height={10}
          width={width}
        />
        <Spacer.Horizontal size={16} />
        <Backbone
          borderRadius='0'
          height={10}
          width={secondaryWidth}
        />
      </Column>
    </Row>
  )
}