import React from 'react'
import { ButtonStatus } from '../../../atoms'
import { DataResponseCart, NoStockProducts } from '../../ModalWithoutStock/ModalWithoutStock'

export interface NoStockModalProps {
  arrFail?: DataResponseCart;
  buttonStatus?: ButtonStatus;
  isOpen?: boolean;
  isOpenModalOutStock?: boolean;
  handleKeepAddress?: () => void;
  saveAddress?: () => void;
}

export const AddressStepShell = ({
  arrFail,
  buttonStatus,
  isOpenModalOutStock,
  handleKeepAddress = () => { return },
  saveAddress
}: NoStockModalProps) => {
  return (
    <>
      <NoStockProducts
        buttonStatus={buttonStatus}
        data={arrFail}
        handleModal={handleKeepAddress}
        isOpen={isOpenModalOutStock}
        onClick={saveAddress}
      />
    </>
  )
}
