import React, { useState } from 'react'
import { Site } from '../../../../../../shared/interfaces/Site'
import { ButtonStatus, Text } from '../../../../../atoms'
import {
  Column,
  Container,
  Row,
  Spacer
} from '../../../../../layout'
import { ValuesProps } from '../../../StoreLocator'
import { AddressForm } from './helpers/AddressForm'
import { LabelInputMap } from './helpers/LabelMapInput'
import styles from './MapModalContent.module.css'

export interface MapModalPropsAlvi {
  buttonStatus?: ButtonStatus;
  children?: React.ReactNode;
  city?: string;
  commune?: string;
  errorAddress?: boolean;
  errorAddressType?: string;
  errorMapMessage?: string;
  inputAdornmentEnd?: boolean;
  isMobile?: boolean;
  isOpen?: boolean;
  name?: string;
  screenHeight?: string;
  selectedAddress?: string;
  showIconAddressRef?: number;
  showIconComment?: number;
  showIconValueAddressStreetNumber?: boolean;
  site?: Site;
  spacerMobile?: boolean;
  streetNumberInput?: boolean;
  titleModal?: string;
  value?: string;
  valueAddressComments?: string;
  valueAddressRef?: string;
  valueAddressStreetNumber?: string;
  values?: ValuesProps;
  back?: () => void;
  buttonAddressMap?: () => void;
  handleBlur?: () => void;
  handleChange?: (e?: React.ChangeEvent<HTMLInputElement>) => void;
  handleFocusComments?: () => void;
  handleModal?: () => void;
  onClick?: () => void;
  onClickClearInput?: (name: string) => void;
  onClickClearInputStreetNumber?: () => void;
}

export const MapModalContent = ({
  buttonStatus,
  city,
  commune,
  inputAdornmentEnd = false,
  isMobile,
  selectedAddress,
  spacerMobile,
  streetNumberInput,
  values,
  buttonAddressMap,
  handleChange,
  onClickClearInput
}: MapModalPropsAlvi): React.ReactElement => {

  const [active, setActive] = useState<boolean | number>(false)
  const handleClick = index => { setActive(index === active ? active : index) }
  const handleFocusComments = () => {
    handleClick(2)
  }
  const handleBlur = () => {
    handleClick(false)
  }

  const formAddressProps = {
    buttonStatus,
    city,
    commune,
    inputAdornmentEnd,
    isMobile,
    selectedAddress,
    showIconAddressRef: values?.addressRef?.length,
    showIconComment: values?.addressComments?.length,
    showIconValueAddressStreetNumber: values?.valueAddressStreetNumber?.length > 0,
    spacerMobile,
    streetNumberInput,
    values,
    buttonAddressMap,
    handleBlur,
    handleChange,
    handleFocusComments,
    onClickClearInput
  }
  return (
    <>
      {spacerMobile && <Spacer.Horizontal size={16} />}
      <Container className={styles['map--modal-ctn']} minHeight='200px' >
        <Row>
          <Column customWidth='auto' padding='5px 20px'>
            <LabelInputMap label='Dirección:' />
            <Row justifyContent='between'>
              <Text color='gray-light' truncate={30}>
                {selectedAddress}
              </Text>
            </Row>
          </Column>
        </Row>
        <AddressForm {...formAddressProps} />
      </Container>
    </>
  )
}