import {
  Column,
  Container,
  Row,
  Spacer
} from '../../../../../../../layout'
import { Text } from '../../../../../../../atoms'
import { AddressItem } from '../AddressItem'
import { Site } from '../../../../../../../../shared/interfaces/Site'
import styles from './PickUpStore.module.css'
import { IStore } from '../IStoresAlvi'

interface PickUpStoreProps {
  heightContainerListAddresses?: string;
  isDelivery?: boolean;
  maxHeightList?: string;
  paddingColumList?: string;
  paddingContainerList?: string;
  showSubtitleList?: boolean;
  site?: Site;
  storeList?: IStore[];
  addressSelector?: (data: object) => void;
}

export const PickUpStore = ({
  heightContainerListAddresses,
  isDelivery,
  maxHeightList,
  paddingColumList,
  paddingContainerList,
  showSubtitleList = true,
  site,
  storeList,
  addressSelector
}: PickUpStoreProps): React.ReactElement => {

  return (
    <Column
      justifyContent='between'
      minHeight={heightContainerListAddresses}
      padding={paddingColumList}
      width='100%'
    >
      <Container
        customClassName={styles.containerAddressList}
        maxHeight={maxHeightList}
        overflow='auto'
        padding={paddingContainerList}
        width='100%'
      >
        <Column>
          <Row>
            <Column>
              {
                storeList?.filter(store => { return store?.pickupPoint?.address?.selected === true }).map((store, i) => {
                  return (
                    <Column key={store.pickupPoint.id} padding='0'>
                      <AddressItem
                        addressSelector={addressSelector}
                        data={store.pickupPoint.address}
                        index={i}
                        isDelivery={isDelivery}
                        isStoreLocator={true}
                        nameIcon='StoreAlvi'
                        selectable={false}
                        site={site}
                      />
                    </Column>
                  )
                })
              }
            </Column>
          </Row>

          <Spacer.Horizontal size={24} />

          { showSubtitleList &&
            <Row>
              <Text fontSize='md' fontWeight='semibold'>
                Tiendas cercanas
              </Text>
            </Row>
          }

          <Spacer.Horizontal size={12} />

          {
            storeList?.filter(store => { return store?.pickupPoint?.address?.selected !== true }).map((store, i) => {
              return (
                <Column key={store.pickupPoint.id}>
                  <AddressItem
                    addressSelector={addressSelector}
                    data={store.pickupPoint.address}
                    index={i}
                    isDelivery={isDelivery}
                    isStoreLocator={true}
                    nameIcon='StoreNotSelected'
                    selectable
                    site={site}
                  />
                </Column>
              )
            })
          }
        </Column>
      </Container>
    </Column>
  )
}