import { AddressModal, MapModal } from './ModalState'
import { FailModal, FailModalProps } from '../FailModal'
import { UserAddressListPropsAlvi } from './ModalState/AddressModal/AddressModalContent'
import { AddressStepShell } from './AddressStepShell'
import { MapModalPropsAlvi } from './ModalState/MapModal/MapModalContent'
import { Site } from '../../../shared/interfaces/Site'
import { DataResponseCart } from '../ModalWithoutStock/ModalWithoutStock'
import { AddressModalPropsAlvi } from './ModalState/AddressModal/AddressModalContent/index'

export interface AddressListPropsAlvi {
  activeMap: boolean;
  city: string;
  description: string;
  failModal: boolean;
  activeIndex?: boolean;
  name: string;
  sla?: string
}
export interface ValuesProps {
  addressComments?: string;
  addressRef?: string;
  search?: string;
  valueAddressStreetNumber?: string;
}
export interface StoreLocatorPropsAlvi {
  AddressProps?: AddressModalPropsAlvi;
  arrFail?: DataResponseCart,
  children?: React.ReactNode;
  currentAddress?: UserAddressListPropsAlvi;
  directionModal?: boolean;
  errorMobileModal?: boolean;
  failModal?: boolean;
  failModalTitle: string;
  failProps?: FailModalProps;
  isMobile?: boolean;
  loadingAddress?: boolean;
  mapModal?: boolean;
  MapProps?: MapModalPropsAlvi;
  site?: Site;
}
export const StoreLocatorAlvi = ({
  failProps,
  MapProps,
  AddressProps,
  failModal,
  mapModal,
  errorMobileModal,
  directionModal,
  children
}: StoreLocatorPropsAlvi): React.ReactElement => {
  if (directionModal) return <AddressModal {...AddressProps} />
  if (errorMobileModal) return <AddressStepShell {...failProps} />
  if (failModal) return <FailModal {...failProps} />
  if (mapModal) return (
    <MapModal {...MapProps}>
      {children}
    </MapModal>
  )
  return <></>
}